import { PublicKey } from '@solana/web3.js'
import BN from 'bn.js'

import { oappIDPDA } from './base-oapp'
import { PROGRAM_ID as OFT_PROGRAM_ID } from './generated/oft'

export const ENDPOINT_SEED = 'Endpoint'
export const MESSAGE_LIB_SEED = 'MessageLib'
export const SEND_LIBRARY_CONFIG_SEED = 'SendLibraryConfig'
export const RECEIVE_LIBRARY_CONFIG_SEED = 'ReceiveLibraryConfig'
export const NONCE_SEED = 'Nonce'
export const PENDING_NONCE_SEED = 'PendingNonce'
export const PAYLOAD_HASH_SEED = 'PayloadHash'
export const COMPOSED_MESSAGE_HASH_SEED = 'ComposedMessageHash'
export const OAPP_SEED = 'OApp'

export const COUNT_SEED = 'Count'
export const REMOTE_SEED = 'Remote'
export const LZ_RECEIVE_TYPES_SEED = 'LzReceiveTypes'
export const LZ_COMPOSE_TYPES_SEED = 'LzComposeTypes'

export const ULN_SEED = 'MessageLib'
export const ULN_CONFIG_SEED = 'UlnConfig'
export const SEND_CONFIG_SEED = 'SendConfig'
export const RECEIVE_CONFIG_SEED = 'ReceiveConfig'
export const OPTIONS_SEED = 'Options'
export const CONFIRMATIONS_SEED = 'Confirmations'
export const WORKER_SEED = 'Worker'

export const DVN_CONFIG_SEED = 'DvnConfig'

export const EVENT_SEED = '__event_authority'

export const EXECUTOR_CONFIG_SEED = 'ExecutorConfig'

export const PRICE_FEED_SEED = 'PriceFeed'

export const OFT_SEED = 'Oft'

export const PEER_SEED = 'Peer'

export const MINT_SEED = 'Mint'

export const ENFORCED_OPTIONS_SEED = 'EnforcedOptions'

/**
 * derive address for endpoint program
 */
export class EndpointPDADeriver {
    program: PublicKey

    constructor(program: PublicKey) {
        this.program = program
    }

    setting(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(ENDPOINT_SEED, 'utf8')], this.program)
    }

    defaultSendLibraryConfig(dstEndpointId: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(SEND_LIBRARY_CONFIG_SEED, 'utf8'),
                // U32 to Uint8Array([0,0,0,0])
                new BN(dstEndpointId).toArrayLike(Buffer, 'be', 4),
            ],
            this.program
        )
    }

    sendLibraryConfig(sender: PublicKey, dstEndpointId: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(SEND_LIBRARY_CONFIG_SEED, 'utf8'),
                sender.toBytes(),
                // U32 to Uint8Array([0,0,0,0])
                new BN(dstEndpointId).toArrayLike(Buffer, 'be', 4),
            ],
            this.program
        )
    }

    /**
     * @param messageLibrary PDA(derive by message lib program)
     */
    messageLibraryInfo(messageLibrary: PublicKey): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(MESSAGE_LIB_SEED, 'utf8'), messageLibrary.toBytes()],
            this.program
        )
    }

    defaultReceiveLibraryConfig(srcEndpointId: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(RECEIVE_LIBRARY_CONFIG_SEED, 'utf8'),
                // U32 to Uint8Array([0,0,0,0])
                new BN(srcEndpointId).toArrayLike(Buffer, 'be', 4),
            ],
            this.program
        )
    }

    receiveLibraryConfig(receiver: PublicKey, srcEndpointId: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(RECEIVE_LIBRARY_CONFIG_SEED, 'utf8'),
                receiver.toBytes(),
                // U32 to Uint8Array([0,0,0,0])
                new BN(srcEndpointId).toArrayLike(Buffer, 'be', 4),
            ],
            this.program
        )
    }

    receiveLibraryTimeout(_dstEndpointId: number): [PublicKey, number] {
        // todo: @zzz find the address of receive library timeout config
        // if it is default config, then get the address of default timeout config
        // otherwise, get the address of the custom one
        return [this.program, 0]
    }

    defaultMessageLib(): [PublicKey, number] {
        return this.messageLibraryInfo(PublicKey.default)
    }

    blockMessageLib(blockMsgLib: PublicKey): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(MESSAGE_LIB_SEED, 'utf8'), blockMsgLib.toBytes()],
            this.program
        )
    }

    /**
     * @param localOapp
     * @param remoteChainId
     * @param remoteOapp
     */
    nonce(localOapp: PublicKey, remoteChainId: number, remoteOapp: Uint8Array): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(NONCE_SEED, 'utf8'),
                localOapp.toBytes(),
                // U32 to Uint8Array([0,0,0,0])
                new BN(remoteChainId).toArrayLike(Buffer, 'be', 4),
                remoteOapp,
            ],
            this.program
        )
    }

    pendingNonce(localOapp: PublicKey, remoteChainId: number, remoteOapp: Uint8Array): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(PENDING_NONCE_SEED, 'utf8'),
                localOapp.toBytes(),
                new BN(remoteChainId).toArrayLike(Buffer, 'be', 4),
                remoteOapp,
            ],
            this.program
        )
    }

    oappRegistry(localOapp: PublicKey): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(OAPP_SEED, 'utf8'), localOapp.toBytes()], this.program)
    }

    /***
     * @param receiver
     * @param srcEid
     * @param sender
     * @param nonce
     * @param payloadHash
     * @srcChainId u32 to Uint8Array([0,0,0,0])
     * @sender [u8; 32] sender Address
     * @nonce u64 to Uint8Array([0,0,0,0,0,0,0,0])
     * @payloadHash [u8; 32]
     */
    payloadHash(receiver: PublicKey, srcEid: number, sender: Uint8Array, nonce: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(PAYLOAD_HASH_SEED, 'utf8'),
                receiver.toBytes(),
                new BN(srcEid).toArrayLike(Buffer, 'be', 4),
                sender,
                new BN(nonce).toArrayLike(Buffer, 'be', 8),
            ],
            this.program
        )
    }

    composedMessage(
        from: PublicKey,
        guid: Uint8Array,
        index: number,
        to: PublicKey,
        messageHash: Uint8Array
    ): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [
                Buffer.from(COMPOSED_MESSAGE_HASH_SEED, 'utf8'),
                from.toBytes(),
                to.toBytes(),
                guid,
                new BN(index).toArrayLike(Buffer, 'be', 2),
                messageHash,
            ],
            this.program
        )
    }
}

/***
 * derive address by message lib program
 */
export class MessageLibPDADeriver {
    program: PublicKey

    constructor(program: PublicKey) {
        this.program = program
    }

    messageLib(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(MESSAGE_LIB_SEED, 'utf8')], this.program)
    }

    sendConfig(eid: number, oapp: PublicKey): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(SEND_CONFIG_SEED, 'utf8'), new BN(eid).toArrayLike(Buffer, 'be', 4), oapp.toBuffer()],
            this.program
        )
    }

    receiveConfig(eid: number, oapp: PublicKey): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(RECEIVE_CONFIG_SEED, 'utf8'), new BN(eid).toArrayLike(Buffer, 'be', 4), oapp.toBuffer()],
            this.program
        )
    }
}

export class UlnPDADeriver extends MessageLibPDADeriver {
    setting(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(ULN_SEED, 'utf8')], this.program)
    }

    config(eid: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(ULN_CONFIG_SEED, 'utf8'), new BN(eid).toArrayLike(Buffer, 'be', 4)],
            this.program
        )
    }

    defaultSendConfig(eid: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(SEND_CONFIG_SEED, 'utf8'), new BN(eid).toArrayLike(Buffer, 'be', 4)],
            this.program
        )
    }

    defaultReceiveConfig(eid: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(RECEIVE_CONFIG_SEED, 'utf8'), new BN(eid).toArrayLike(Buffer, 'be', 4)],
            this.program
        )
    }

    options(eit: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(OPTIONS_SEED, 'utf8'), new BN(eit).toArrayLike(Buffer, 'be', 4)],
            this.program
        )
    }

    workerConfig(worker: PublicKey): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(WORKER_SEED, 'utf8'), worker.toBuffer()], this.program)
    }

    confirmations(headerHash: Uint8Array, payloadHash: Uint8Array, dvn: PublicKey): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(CONFIRMATIONS_SEED, 'utf8'), headerHash, payloadHash, dvn.toBytes()],
            this.program
        )
    }
}

/**
 * derive address by base oapp program
 */
export class OAppBasePDADeriver {
    program: PublicKey

    constructor(program: PublicKey) {
        this.program = program
    }

    remote(dstChainId: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(REMOTE_SEED), new BN(dstChainId).toArrayLike(Buffer, 'be', 4)],
            this.program
        )
    }

    lzReceiveTypesAccounts(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(LZ_RECEIVE_TYPES_SEED, 'utf8')], this.program)
    }

    lzComposeTypesAccounts(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(LZ_COMPOSE_TYPES_SEED, 'utf8')], this.program)
    }
}

/**
 * derive address by omnicounter program
 */
export class OmniCounterPDADeriver extends OAppBasePDADeriver {
    constructor(
        program: PublicKey,
        public counterId: number
    ) {
        super(program)
    }

    count(): [PublicKey, number] {
        // return PublicKey.findProgramAddressSync([Buffer.from(COUNT_SEED, 'utf8')], this.program)
        return oappIDPDA(this.program, COUNT_SEED, this.counterId)
    }

    remote(dstChainId: number): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(REMOTE_SEED), this.count()[0].toBytes(), new BN(dstChainId).toArrayLike(Buffer, 'be', 4)],
            this.program
        )
    }

    lzReceiveTypesAccounts(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(LZ_RECEIVE_TYPES_SEED, 'utf8'), this.count()[0].toBytes()],
            this.program
        )
    }

    lzComposeTypesAccounts(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync(
            [Buffer.from(LZ_COMPOSE_TYPES_SEED, 'utf8'), this.count()[0].toBytes()],
            this.program
        )
    }
}

export class DVNDeriver {
    constructor(public program: PublicKey) {}

    authority(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from('dvn', 'utf8')], this.program)
    }

    config(): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from(DVN_CONFIG_SEED, 'utf8')], this.program)
    }

    executeHash(digestHash: Buffer): [PublicKey, number] {
        return PublicKey.findProgramAddressSync([Buffer.from('ExecuteHash', 'utf8'), digestHash], this.program)
    }
}

