export interface PacketPath {
    srcEid: number
    sender: string
    dstEid: number
    receiver: string
}

export type PacketHeader = {
    version: number
    nonce: string
} & PacketPath

export type Packet = PacketHeader & {
    guid: string
    message: string
    // derived
    payload: string // payload = guid + message
}

// message origin
export interface MessageOrigin {
    srcEid: number
    sender: string
    nonce: string
}

export function packetToMessageOrigin(packet: Packet): MessageOrigin {
    return {
        srcEid: packet.srcEid,
        sender: packet.sender,
        nonce: packet.nonce,
    }
}
