export async function isAccountInitialized(
    connection: Connection,
    account: PublicKey,
    commitmentOrConfig: Commitment | GetAccountInfoConfig = 'confirmed'
): Promise<boolean> {
    return connection.getAccountInfo(account, commitmentOrConfig).then((res) => {
        return res !== null
    })
}

export async function createNonceTx(
    connection: Connection,
    nonceAccount: PublicKey,
    nonceAuthority: PublicKey,
    instructions: TransactionInstruction[],
    commitmentOrConfig: Commitment | GetNonceConfig = 'confirmed'
): Promise<Transaction> {
    const nonceAccountInfo = await connection.getNonce(nonceAccount, commitmentOrConfig)
    if (nonceAccountInfo === null) {
        throw new Error('Invalid nonce account')
    }
    const { nonce } = nonceAccountInfo

    const nonceAdvanceIx = SystemProgram.nonceAdvance({
        noncePubkey: nonceAccount,
        authorizedPubkey: nonceAuthority,
    })

    return new Transaction({
        recentBlockhash: nonce,
        nonceInfo: {
            nonce,
            nonceInstruction: nonceAdvanceIx,
        },
    }).add(...instructions)
}

export async function createInitAdapterOftIx(
    payer: PublicKey,
    admin: PublicKey,
    mint: PublicKey,
    lzDelegate: PublicKey,
    escrow: PublicKey,
    sharedDecimals = 6,
    tokenProgram: PublicKey = TOKEN_PROGRAM_ID
): Promise<TransactionInstruction> {
    const [oftConfig] = PublicKey.findProgramAddressSync(
        [Buffer.from(OFT_SEED, 'utf-8'), escrow.toBuffer()],
        OFT_PROGRAM_ID
    )
    const [oAppRegistry] = PublicKey.findProgramAddressSync(
        [Buffer.from(OAPP_SEED, 'utf-8'), oftConfig.toBuffer()],
        ENDPOINT_PROGRAM_ID
    )
    const [endpointEventAuthority] = PublicKey.findProgramAddressSync(
        [Buffer.from(EVENT_SEED, 'utf-8')],
        ENDPOINT_PROGRAM_ID
    )
    const [lzReceiveTypes] = PublicKey.findProgramAddressSync(
        [Buffer.from(LZ_RECEIVE_TYPES_SEED, 'utf-8'), oftConfig.toBuffer()],
        OFT_PROGRAM_ID
    )
    const registerOappIx = endpointInstructions.createRegisterOappInstruction(
        {
            payer: payer,
            oapp: oftConfig,
            oappRegistry: oAppRegistry,
            eventAuthority: endpointEventAuthority,
            program: OFT_PROGRAM_ID,
        },
        {
            params: {
                delegate: lzDelegate,
            },
        }
    )
    for (const acc of registerOappIx.keys) {
        acc.isSigner = false
    }
    return oftInstructions.createInitAdapterOftInstruction(
        {
            payer: payer,
            oftConfig: oftConfig,
            tokenMint: mint,
            tokenEscrow: escrow,
            lzReceiveTypesAccounts: lzReceiveTypes,
            tokenProgram: tokenProgram,
            anchorRemainingAccounts: [
                {
                    pubkey: EndpointProgramId,
                    isSigner: false,
                    isWritable: false,
                },
            ].concat(
                ...registerOappIx.keys,
                {
                    pubkey: endpointEventAuthority,
                    isSigner: false,
                    isWritable: false,
                },
                {
                    pubkey: EndpointProgramId,
                    isSigner: false,
                    isWritable: false,
                }
            ),
        },
        {
            params: {
                admin: admin,
                sharedDecimals: sharedDecimals,
                endpointProgram: EndpointProgramId,
            },
        }
    )
}

