export async function createSetPeerIx(
    admin: PublicKey,
    oftConfig: PublicKey,
    dstEid: number,
    peer: number[]
): Promise<TransactionInstruction> {
    if (peer.length !== 32) {
        throw new Error('Peer must be 32 bytes (left-padded with zeroes)')
    }
    if (dstEid % 30000 == 0) {
        throw new Error('Invalid dstEid')
    }
    const [peerPda] = PublicKey.findProgramAddressSync(
        [Buffer.from(PEER_SEED, 'utf-8'), oftConfig.toBuffer(), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        OFT_PROGRAM_ID
    )
    return oftInstructions.createSetPeerInstruction(
        {
            admin: admin,
            peer: peerPda,
            oftConfig: oftConfig,
        },
        {
            params: {
                dstEid: dstEid,
                peer: peer,
            },
        },
        OFT_PROGRAM_ID
    )
}

