export async function createSetSendLibraryIx(
    signer: PublicKey,
    oftConfig: PublicKey,
    sendLibraryProgram: PublicKey,
    dstEid: number
): Promise<TransactionInstruction> {
    const [newSendLib] = PublicKey.findProgramAddressSync([Buffer.from(MESSAGE_LIB_SEED, 'utf8')], sendLibraryProgram)

    const [oAppRegistry] = PublicKey.findProgramAddressSync(
        [Buffer.from(OAPP_SEED, 'utf-8'), oftConfig.toBuffer()],
        ENDPOINT_PROGRAM_ID
    )
    const [sendLibraryConfig] = PublicKey.findProgramAddressSync(
        [
            Buffer.from(SEND_LIBRARY_CONFIG_SEED, 'utf-8'),
            oftConfig.toBuffer(),
            new BN(dstEid).toArrayLike(Buffer, 'be', 4),
        ],
        ENDPOINT_PROGRAM_ID
    )
    const [sendLibraryInfo] = PublicKey.findProgramAddressSync(
        [Buffer.from(MESSAGE_LIB_SEED, 'utf-8'), newSendLib.toBuffer()],
        ENDPOINT_PROGRAM_ID
    )
    const [eventAuthority] = PublicKey.findProgramAddressSync([Buffer.from(EVENT_SEED, 'utf-8')], ENDPOINT_PROGRAM_ID)
    return endpointInstructions.createSetSendLibraryInstruction(
        {
            signer: signer,
            oappRegistry: oAppRegistry,
            sendLibraryConfig: sendLibraryConfig,
            messageLibInfo: sendLibraryInfo,
            eventAuthority: eventAuthority,
            program: ENDPOINT_PROGRAM_ID,
        },
        {
            params: {
                sender: oftConfig,
                eid: dstEid,
                newLib: newSendLib,
            },
        },
        ENDPOINT_PROGRAM_ID
    )
}

