export async function createInitConfigIx(
    signer: PublicKey,
    oftConfig: PublicKey,
    messageLibProgram: PublicKey,
    dstEid: number
): Promise<TransactionInstruction> {
    const [oAppRegistry] = PublicKey.findProgramAddressSync(
        [Buffer.from(OAPP_SEED, 'utf-8'), oftConfig.toBytes()],
        ENDPOINT_PROGRAM_ID
    )
    const [messageLib] = PublicKey.findProgramAddressSync([Buffer.from(MESSAGE_LIB_SEED, 'utf-8')], messageLibProgram)
    const [messageLibInfo] = PublicKey.findProgramAddressSync(
        [Buffer.from(MESSAGE_LIB_SEED, 'utf-8'), messageLib.toBytes()],
        ENDPOINT_PROGRAM_ID
    )
    const [endpointSettings] = PublicKey.findProgramAddressSync(
        [Buffer.from(ENDPOINT_SEED, 'utf-8')],
        ENDPOINT_PROGRAM_ID
    )
    const [sendConfig] = PublicKey.findProgramAddressSync(
        [Buffer.from(SEND_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4), oftConfig.toBytes()],
        messageLibProgram
    )
    const [receiveConfig] = PublicKey.findProgramAddressSync(
        [Buffer.from(RECEIVE_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4), oftConfig.toBytes()],
        messageLibProgram
    )
    const initConfigMessageLibIx = ulnInstructions.createInitConfigInstruction(
        {
            endpoint: endpointSettings,
            payer: signer,
            uln: messageLib,
            sendConfig: sendConfig,
            receiveConfig: receiveConfig,
        },
        {
            params: {
                oapp: oftConfig,
                eid: dstEid,
            },
        }
    )
    for (const acc of initConfigMessageLibIx.keys) {
        if (!signer.equals(acc.pubkey)) {
            acc.isSigner = false
        }
    }
    return endpointInstructions.createInitConfigInstruction(
        {
            delegate: signer,
            oappRegistry: oAppRegistry,
            messageLibInfo: messageLibInfo,
            messageLib: messageLib,
            messageLibProgram: messageLibProgram,
            anchorRemainingAccounts: initConfigMessageLibIx.keys.slice(1),
        },
        {
            params: {
                oapp: oftConfig,
                eid: dstEid,
            },
        }
    )
}
export async function createSetConfigIx(
    signer: PublicKey,
    oftConfig: PublicKey,
    dstEid: number,
    configType: ConfigType,
    config: Uint8Array,
    messageLibProgram: PublicKey = ULN_PROGRAM_ID
): Promise<TransactionInstruction> {
    const [oAppRegistry] = PublicKey.findProgramAddressSync(
        [Buffer.from(OAPP_SEED, 'utf-8'), oftConfig.toBytes()],
        ENDPOINT_PROGRAM_ID
    )
    const [messageLib] = PublicKey.findProgramAddressSync([Buffer.from(MESSAGE_LIB_SEED, 'utf-8')], messageLibProgram)
    const [messageLibInfo] = PublicKey.findProgramAddressSync(
        [Buffer.from(MESSAGE_LIB_SEED, 'utf-8'), messageLib.toBytes()],
        ENDPOINT_PROGRAM_ID
    )
    const [endpointSettings] = PublicKey.findProgramAddressSync(
        [Buffer.from(ENDPOINT_SEED, 'utf-8')],
        ENDPOINT_PROGRAM_ID
    )
    const [defaultSendConfig] = PublicKey.findProgramAddressSync(
        [Buffer.from(SEND_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        messageLibProgram
    )
    const [sendConfig] = PublicKey.findProgramAddressSync(
        [Buffer.from(SEND_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4), oftConfig.toBytes()],
        messageLibProgram
    )
    const [defaultReceiveConfig] = PublicKey.findProgramAddressSync(
        [Buffer.from(RECEIVE_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        messageLibProgram
    )
    const [receiveConfig] = PublicKey.findProgramAddressSync(
        [Buffer.from(RECEIVE_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4), oftConfig.toBytes()],
        messageLibProgram
    )
    const [messageLibEventAuthority] = PublicKey.findProgramAddressSync(
        [Buffer.from(EVENT_SEED, 'utf-8')],
        messageLibProgram
    )
    const [ulnSettings] = PublicKey.findProgramAddressSync([Buffer.from(ULN_SEED, 'utf-8')], messageLibProgram)

    const messageLibSetConfigIx = ulnInstructions.createSetConfigInstruction(
        {
            endpoint: endpointSettings,
            uln: ulnSettings,
            sendConfig: sendConfig,
            receiveConfig: receiveConfig,
            defaultSendConfig: defaultSendConfig,
            defaultReceiveConfig: defaultReceiveConfig,
            eventAuthority: messageLibEventAuthority,
            program: messageLibProgram,
        },
        {
            params: {
                oapp: oftConfig,
                eid: dstEid,
                configType: configType,
                config: config,
            },
        },
        messageLibProgram
    )
    for (const acc of messageLibSetConfigIx.keys) {
        if (!signer.equals(acc.pubkey)) {
            acc.isSigner = false
        }
    }
    return endpointInstructions.createSetConfigInstruction(
        {
            signer: signer,
            oappRegistry: oAppRegistry,
            messageLibInfo: messageLibInfo,
            messageLib: messageLib,
            messageLibProgram: messageLibProgram,
            anchorRemainingAccounts: messageLibSetConfigIx.keys.slice(1),
        },
        {
            params: {
                oapp: oftConfig,
                eid: dstEid,
                configType: configType,
                config: config,
            },
        },
        ENDPOINT_PROGRAM_ID
    )
}

