export async function sendWithUln(
    connection: Connection,
    payer: PublicKey,
    tokenMint: PublicKey,
    tokenSource: PublicKey,
    dstEid: number,
    amountLd: bigint,
    minAmountLd: bigint,
    options: Uint8Array,
    to: number[],
    nativeFee: bigint,
    tokenEscrow: PublicKey | undefined = undefined,
    lzTokenFee = 0n,
    tokenProgram: PublicKey = TOKEN_PROGRAM_ID,
    composeMsg: Uint8Array | null = null
): Promise<TransactionInstruction> {
    const [oftInstance] = PublicKey.findProgramAddressSync(
        [Buffer.from(OFT_SEED, 'utf-8'), tokenEscrow ? tokenEscrow.toBuffer() : tokenMint.toBuffer()],
        OFT_PROGRAM_ID
    )
    const [peer] = PublicKey.findProgramAddressSync(
        [Buffer.from(PEER_SEED, 'utf-8'), oftInstance.toBuffer(), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        OFT_PROGRAM_ID
    )
    const [oftEventAuthority] = PublicKey.findProgramAddressSync([Buffer.from(EVENT_SEED, 'utf-8')], OFT_PROGRAM_ID)
    const peerInfo = await Peer.fromAccountAddress(connection, peer)
    const peerAddress = peerInfo.address
    const [enforcedOptions] = PublicKey.findProgramAddressSync(
        [
            Buffer.from(ENFORCED_OPTIONS_SEED, 'utf-8'),
            oftInstance.toBuffer(),
            new BN(dstEid).toArrayLike(Buffer, 'be', 4),
        ],
        OFT_PROGRAM_ID
    )
    const messageLib = new Uln(ULN_PROGRAM_ID)
    const endpoint = new Endpoint(ENDPOINT_PROGRAM_ID)
    const remainingAccounts = await endpoint.getSendIXAccountMetaForCPI(
        connection,
        payer,
        {
            srcEid: SOLANA_EID,
            sender: hexlify(oftInstance.toBytes()),
            dstEid: dstEid,
            receiver: hexlify(peerAddress),
        },
        messageLib
    )
    return oftInstructions.createSendInstruction(
        {
            signer: payer,
            peer: peer,
            enforcedOptions: enforcedOptions,
            oftConfig: oftInstance,
            tokenSource: tokenSource,
            tokenEscrow: tokenEscrow,
            tokenMint: tokenMint,
            tokenProgram: tokenProgram,
            eventAuthority: oftEventAuthority,
            program: OFT_PROGRAM_ID,
            anchorRemainingAccounts: remainingAccounts,
        },
        {
            params: {
                dstEid: dstEid,
                to: to,
                amountLd: new BN(amountLd.toString()),
                minAmountLd: new BN(minAmountLd.toString()),
                options: options,
                composeMsg: composeMsg,
                nativeFee: new BN(nativeFee.toString()),
                lzTokenFee: new BN(lzTokenFee.toString()),
            },
        }
    )
}

export async function createInitNonceIx(
    delegate: PublicKey, // payer
    dstEid: number,
    oftInstance: PublicKey,
    remoteOappAddr: Uint8Array
): Promise<TransactionInstruction> {
    const [nonce] = PublicKey.findProgramAddressSync(
        [Buffer.from(NONCE_SEED), oftInstance.toBuffer(), new BN(dstEid).toArrayLike(Buffer, 'be', 4), remoteOappAddr],
        ENDPOINT_PROGRAM_ID
    )
    const [pendingNonce] = PublicKey.findProgramAddressSync(
        [
            Buffer.from(PENDING_NONCE_SEED),
            oftInstance.toBuffer(),
            new BN(dstEid).toArrayLike(Buffer, 'be', 4),
            remoteOappAddr,
        ],
        ENDPOINT_PROGRAM_ID
    )
    const [oAppRegistry] = PublicKey.findProgramAddressSync(
        [Buffer.from(OAPP_SEED, 'utf-8'), oftInstance.toBuffer()],
        ENDPOINT_PROGRAM_ID
    )

    return endpointInstructions.createInitNonceInstruction(
        {
            nonce: nonce,
            pendingInboundNonce: pendingNonce,
            oappRegistry: oAppRegistry,
            delegate: delegate,
        },
        {
            params: {
                localOapp: oftInstance,
                remoteOapp: Array.from(remoteOappAddr),
                remoteEid: dstEid,
            },
        },
        ENDPOINT_PROGRAM_ID
    )
}
export async function quoteOft(
    connection: Connection,
    payer: PublicKey,
    tokenMint: PublicKey,
    dstEid: number,
    amountLd: bigint,
    minAmountLd: bigint,
    options: Uint8Array,
    to: number[],
    tokenEscrow: PublicKey | undefined = undefined,
    payInLzToken = false,
    composeMsg: Uint8Array | null = null
): Promise<{
    oftLimits: {
        minAmountLd: string
        maxAmountLd: string
    }
    oftFeeDetail: {
        feeAmountLd: string
        description: string
    }
    oftReceipt: {
        amountSentLd: string
        amountReceivedLd: string
    }
}> {
    const [oftInstance] = PublicKey.findProgramAddressSync(
        [Buffer.from(OFT_SEED, 'utf-8'), tokenMint.toBuffer()],
        OFT_PROGRAM_ID
    )
    const [peer] = PublicKey.findProgramAddressSync(
        [Buffer.from(PEER_SEED, 'utf-8'), oftInstance.toBuffer(), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        OFT_PROGRAM_ID
    )
    const ix = oftInstructions.createQuoteOftInstruction(
        {
            oftConfig: oftInstance,
            peer: peer,
            tokenMint: tokenMint,
        },
        {
            params: {
                dstEid: dstEid,
                to: to,
                amountLd: new BN(amountLd.toString()),
                minAmountLd: new BN(minAmountLd.toString()),
                options: options,
                payInLzToken: payInLzToken,
                composeMsg: composeMsg,
            },
        }
    )
    const returnedValues = await simulateTransaction(connection, [ix], ix.programId, payer, 'confirmed')
    const oftLimits = oFTLimitsBeet.read(returnedValues, 0)
    const [oftFeeDetail] = oFTFeeDetailBeet.deserialize(returnedValues, 20)
    const oftReceipt = oFTReceiptBeet.read(returnedValues, returnedValues.length - oFTLimitsBeet.byteSize)
    return {
        oftLimits: {
            minAmountLd: oftLimits.minAmountLd.toString(),
            maxAmountLd: oftLimits.maxAmountLd.toString(),
        },
        oftFeeDetail: {
            feeAmountLd: oftFeeDetail.feeAmountLd.toString(),
            description: oftFeeDetail.description,
        },
        oftReceipt: {
            amountSentLd: oftReceipt.amountSentLd.toString(),
            amountReceivedLd: oftReceipt.amountReceivedLd.toString(),
        },
    }
}

export async function quoteWithUln(
    connection: Connection,
    payer: PublicKey,
    tokenMint: PublicKey,
    dstEid: number,
    amountLd: bigint,
    minAmountLd: bigint,
    options: Uint8Array,
    to: number[],
    tokenEscrow: PublicKey | undefined = undefined,
    payInLzToken = false,
    composeMsg: Uint8Array | null = null
): Promise<{ nativeFee: bigint; lzTokenFee: bigint }> {
    const [oftInstance] =
        tokenEscrow === undefined
            ? PublicKey.findProgramAddressSync([Buffer.from(OFT_SEED, 'utf-8'), tokenMint.toBuffer()], OFT_PROGRAM_ID)
            : PublicKey.findProgramAddressSync([Buffer.from(OFT_SEED, 'utf-8'), tokenEscrow.toBuffer()], OFT_PROGRAM_ID)
    const [peer] = PublicKey.findProgramAddressSync(
        [Buffer.from(PEER_SEED, 'utf-8'), oftInstance.toBytes(), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        OFT_PROGRAM_ID
    )
    const peerinfo = await connection.getAccountInfo(peer)
    if (peerinfo === null) {
        throw new Error('Peer not found')
    }
    const peerAddress = peerinfo.data.subarray(8, 32 + 8)
    const [enforcedOptions] = PublicKey.findProgramAddressSync(
        [
            Buffer.from(ENFORCED_OPTIONS_SEED, 'utf-8'),
            oftInstance.toBuffer(),
            new BN(dstEid).toArrayLike(Buffer, 'be', 4),
        ],
        OFT_PROGRAM_ID
    )
    const messageLib = new Uln(ULN_PROGRAM_ID)
    const endpoint = new Endpoint(ENDPOINT_PROGRAM_ID)
    const remainingAccounts = await endpoint.getQuoteIXAccountMetaForCPI(
        connection,
        payer,
        {
            srcEid: SOLANA_EID,
            sender: hexlify(oftInstance.toBytes()),
            dstEid: dstEid,
            receiver: hexlify(peerAddress),
        },
        messageLib
    )
    const ix = oftInstructions.createQuoteInstruction(
        {
            oftConfig: oftInstance,
            peer: peer,
            enforcedOptions: enforcedOptions,
            tokenMint: tokenMint,
            anchorRemainingAccounts: remainingAccounts,
        },
        {
            params: {
                dstEid: dstEid,
                to: to,
                amountLd: new BN(amountLd.toString()),
                minAmountLd: new BN(minAmountLd.toString()),
                options: options,
                payInLzToken: payInLzToken,
                composeMsg: composeMsg,
            },
        }
    )

    // let tableAddr: PublicKey | undefined
    // let instructions: TransactionInstruction[] | undefined
    // {
    //     ;({ instructions, address: tableAddr } = await generateAddressLookupTable(
    //         connection,
    //         payer.publicKey,
    //         payer.publicKey,
    //         ix.keys.map((k) => k.pubkey)
    //     ))
    //     const tx = await buildVersionedTransaction(connection, payer.publicKey, instructions)
    //     tx.sign([payer])
    //     const txHash = await connection.sendTransaction(tx, {
    //         preflightCommitment: 'confirmed',
    //     })
    //     await connection.confirmTransaction(txHash, 'confirmed')
    // }

    const modifyComputeUnits = ComputeBudgetProgram.setComputeUnitLimit({
        units: 1000000,
    })
    const buffer = await simulateTransaction(connection, [modifyComputeUnits, ix], ix.programId, payer, 'confirmed')
    const fee = EndpointTypes.messagingFeeBeet.read(buffer, 0)
    return { nativeFee: BigInt(fee.nativeFee.toString()), lzTokenFee: BigInt(fee.lzTokenFee.toString()) }
}

