export async function getEndpointConfig(
    connection: Connection,
    oftInstance: PublicKey,
    dstEid: number
): Promise<{
    sendLibraryConfig: SendLibraryConfig & { ulnSendConfig?: SendConfig }
    receiveLibraryConfig: ReceiveLibraryConfig & { ulnReceiveConfig?: ReceiveConfig }
}> {
    const [sendLib] = PublicKey.findProgramAddressSync(
        [
            Buffer.from(SEND_LIBRARY_CONFIG_SEED, 'utf-8'),
            oftInstance.toBuffer(),
            new BN(dstEid).toArrayLike(Buffer, 'be', 4),
        ],
        ENDPOINT_PROGRAM_ID
    )
    const [defaultSendLib] = PublicKey.findProgramAddressSync(
        [Buffer.from(SEND_LIBRARY_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        ENDPOINT_PROGRAM_ID
    )
    const [receiveLib] = PublicKey.findProgramAddressSync(
        [
            Buffer.from(RECEIVE_LIBRARY_CONFIG_SEED, 'utf-8'),
            oftInstance.toBuffer(),
            new BN(dstEid).toArrayLike(Buffer, 'be', 4),
        ],
        ENDPOINT_PROGRAM_ID
    )
    const [defaultReceiveLib] = PublicKey.findProgramAddressSync(
        [Buffer.from(RECEIVE_LIBRARY_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        ENDPOINT_PROGRAM_ID
    )
    const [ulnSettings] = PublicKey.findProgramAddressSync([Buffer.from(ULN_SEED, 'utf-8')], ULN_PROGRAM_ID)

    let sendLibraryConfig: SendLibraryConfig & { ulnSendConfig?: SendConfig } =
        await EndpointAccounts.SendLibraryConfig.fromAccountAddress(connection, sendLib)
    let receiveLibraryConfig: ReceiveLibraryConfig & { ulnReceiveConfig?: ReceiveConfig } =
        await EndpointAccounts.ReceiveLibraryConfig.fromAccountAddress(connection, receiveLib)
    const defaultSendLibraryConfig: SendLibraryConfig & { ulnSendConfig?: SendConfig } =
        await EndpointAccounts.SendLibraryConfig.fromAccountAddress(connection, defaultSendLib)
    const defaultReceiveLibraryConfig: ReceiveLibraryConfig & { ulnReceiveConfig?: ReceiveConfig } =
        await EndpointAccounts.ReceiveLibraryConfig.fromAccountAddress(connection, defaultReceiveLib)

    const nil64 = new BN('fffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff', 'hex')
    const nil8 = 255

    if (sendLibraryConfig.messageLib.equals(PublicKey.default)) {
        sendLibraryConfig = defaultSendLibraryConfig
    }
    if (receiveLibraryConfig.messageLib.equals(PublicKey.default)) {
        receiveLibraryConfig = defaultReceiveLibraryConfig
    }
    // get the uln config if necessary
    if (sendLibraryConfig.messageLib.equals(ulnSettings)) {
        const [ulnDefaultSendConfigPDA] = PublicKey.findProgramAddressSync(
            [Buffer.from(SEND_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
            ULN_PROGRAM_ID
        )
        const [ulnSendConfigPDA] = PublicKey.findProgramAddressSync(
            [
                Buffer.from(SEND_CONFIG_SEED, 'utf-8'),
                new BN(dstEid).toArrayLike(Buffer, 'be', 4),
                oftInstance.toBuffer(),
            ],
            ULN_PROGRAM_ID
        )
        const ulnSendConfig = await UlnAccounts.SendConfig.fromAccountAddress(connection, ulnSendConfigPDA)
        const ulnDefaultSendConfig = await UlnAccounts.SendConfig.fromAccountAddress(
            connection,
            ulnDefaultSendConfigPDA
        )
        // get the uln config for the send library
        if (nil64.eq(new BN(ulnSendConfig.uln.confirmations.toString()))) {
            ulnSendConfig.uln.confirmations = 0
        } else if (ulnSendConfig.uln.confirmations == 0) {
            ulnSendConfig.uln.confirmations = ulnDefaultSendConfig.uln.confirmations
        }
        if (ulnSendConfig.uln.requiredDvnCount == nil8) {
            ulnSendConfig.uln.requiredDvnCount = 0
        } else if (ulnSendConfig.uln.requiredDvnCount == 0) {
            ulnSendConfig.uln.requiredDvnCount = ulnDefaultSendConfig.uln.requiredDvnCount
            ulnSendConfig.uln.requiredDvns = ulnDefaultSendConfig.uln.requiredDvns
        }
        if (ulnSendConfig.uln.optionalDvnCount == nil8) {
            ulnSendConfig.uln.optionalDvnCount = 0
            ulnSendConfig.uln.optionalDvnThreshold = 0
        } else if (ulnSendConfig.uln.optionalDvnCount == 0) {
            ulnSendConfig.uln.optionalDvnCount = ulnDefaultSendConfig.uln.optionalDvnCount
            ulnSendConfig.uln.optionalDvnThreshold = ulnDefaultSendConfig.uln.optionalDvnThreshold
            ulnSendConfig.uln.optionalDvns = ulnDefaultSendConfig.uln.optionalDvns
        }
        sendLibraryConfig.ulnSendConfig = ulnSendConfig
    }
    if (receiveLibraryConfig.messageLib.equals(ulnSettings)) {
        const [ulnDefaultReceiveConfigPDA] = PublicKey.findProgramAddressSync(
            [Buffer.from(SEND_CONFIG_SEED, 'utf-8'), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
            ULN_PROGRAM_ID
        )
        const [ulnReceiveConfigPDA] = PublicKey.findProgramAddressSync(
            [
                Buffer.from(SEND_CONFIG_SEED, 'utf-8'),
                new BN(dstEid).toArrayLike(Buffer, 'be', 4),
                oftInstance.toBuffer(),
            ],
            ULN_PROGRAM_ID
        )
        const ulnReceiveConfig = await UlnAccounts.ReceiveConfig.fromAccountAddress(connection, ulnReceiveConfigPDA)
        const ulnDefaultReceiveConfig = await UlnAccounts.ReceiveConfig.fromAccountAddress(
            connection,
            ulnDefaultReceiveConfigPDA
        )
        if (nil64.eq(new BN(ulnReceiveConfig.uln.confirmations.toString()))) {
            ulnReceiveConfig.uln.confirmations = 0
        } else if (ulnReceiveConfig.uln.confirmations == 0) {
            ulnReceiveConfig.uln.confirmations = ulnDefaultReceiveConfig.uln.confirmations
        }
        if (ulnReceiveConfig.uln.requiredDvnCount == nil8) {
            ulnReceiveConfig.uln.requiredDvnCount = 0
        } else if (ulnReceiveConfig.uln.requiredDvnCount == 0) {
            ulnReceiveConfig.uln.requiredDvnCount = ulnDefaultReceiveConfig.uln.requiredDvnCount
            ulnReceiveConfig.uln.requiredDvns = ulnDefaultReceiveConfig.uln.requiredDvns
        }
        if (ulnReceiveConfig.uln.optionalDvnCount == nil8) {
            ulnReceiveConfig.uln.optionalDvnCount = 0
            ulnReceiveConfig.uln.optionalDvnThreshold = 0
        } else if (ulnReceiveConfig.uln.optionalDvnCount == 0) {
            ulnReceiveConfig.uln.optionalDvnCount = ulnDefaultReceiveConfig.uln.optionalDvnCount
            ulnReceiveConfig.uln.optionalDvnThreshold = ulnDefaultReceiveConfig.uln.optionalDvnThreshold
            ulnReceiveConfig.uln.optionalDvns = ulnDefaultReceiveConfig.uln.optionalDvns
        }
        receiveLibraryConfig.ulnReceiveConfig = ulnReceiveConfig
    }
    return {
        sendLibraryConfig,
        receiveLibraryConfig,
    }
    // send lib address, if blocked then just return that, otherwise return the uln config
    // send lib executor, dvns etc.
    // receive lib executor, dvns
}

// returns the hex string of the peer address
export async function getPeerAddress(connection: Connection, oftInstance: PublicKey, dstEid: number): Promise<string> {
    const [peer] = PublicKey.findProgramAddressSync(
        [Buffer.from(PEER_SEED, 'utf-8'), oftInstance.toBytes(), new BN(dstEid).toArrayLike(Buffer, 'be', 4)],
        OFT_PROGRAM_ID
    )
    const peerinfo = (await connection.getAccountInfo(peer))!
    return hexlify(peerinfo.data.subarray(8, 32 + 8))
}

export async function getDelegate(connection: Connection, oftInstance: PublicKey): Promise<PublicKey> {
    const [oAppRegistry] = PublicKey.findProgramAddressSync(
        [Buffer.from(OAPP_SEED, 'utf-8'), oftInstance.toBuffer()],
        ENDPOINT_PROGRAM_ID
    )
    const oAppRegistryInfo = (await connection.getAccountInfo(oAppRegistry))!
    return new PublicKey(oAppRegistryInfo.data.subarray(8, 40))
}

export async function getEnforcedOptions(connection: Connection, oftInstance: PublicKey, dstEid: number): Promise<any> {
    const [enforcedOptions] = PublicKey.findProgramAddressSync(
        [
            Buffer.from(ENFORCED_OPTIONS_SEED, 'utf-8'),
            oftInstance.toBuffer(),
            new BN(dstEid).toArrayLike(Buffer, 'be', 4),
        ],
        OFT_PROGRAM_ID
    )
    const enforcedOptionsInfo = (await connection.getAccountInfo(enforcedOptions))!
    return enforcedOptionsBeet.deserialize(enforcedOptionsInfo.data)
}
