import {
    AccountMeta,
    Commitment,
    Connection,
    GetAccountInfoConfig,
    PublicKey,
    TransactionInstruction,
} from '@solana/web3.js'

import { PacketPath, PacketV1Codec } from '@layerzerolabs/lz-v2-utilities'

import * as accounts from './generated/simple_messagelib/accounts'
import * as errors from './generated/simple_messagelib/errors'
import * as instructions from './generated/simple_messagelib/instructions'
import * as types from './generated/simple_messagelib/types'
import { EndpointPDADeriver, MessageLibPDADeriver } from './pda-deriver'

import { EndpointProgram, MessageLibInterface } from '.'

export { accounts, instructions, types, errors }
export { PROGRAM_ID } from './generated/simple_messagelib'

export class SimpleMessageLib implements MessageLibInterface {
    deriver: MessageLibPDADeriver

    constructor(public program: PublicKey) {
        this.deriver = new MessageLibPDADeriver(program)
    }

    async initSimpleMessageLib(
        connection: Connection,
        endpointProgram: PublicKey,
        payer: PublicKey,
        admin: PublicKey,
        eid: number,
        nativeFee: number,
        lzTokenFee = 0
    ): Promise<TransactionInstruction | null> {
        const [messageLibPda] = this.deriver.messageLib()
        const info = await connection.getAccountInfo(messageLibPda)
        if (info) {
            return null
        }

        const [endpointAuth] = new EndpointPDADeriver(endpointProgram).messageLibraryInfo(messageLibPda)
        return instructions.createInitMessageLibInstruction(
            {
                payer,
                messageLib: messageLibPda,
            } satisfies instructions.InitMessageLibInstructionAccounts,
            {
                params: {
                    endpointProgram: endpointProgram,
                    endpoint: endpointAuth,
                    eid,
                    admin,
                    fee: nativeFee,
                    lzTokenFee,
                } satisfies types.InitMessageLibParams,
            },
            this.program
        )
    }

    setWhitelistCaller(admin: PublicKey, newCaller: PublicKey): TransactionInstruction {
        const [messageLibPDA] = this.deriver.messageLib()
        return instructions.createSetWlCallerInstruction(
            {
                admin,
                messageLib: messageLibPDA,
            } satisfies instructions.SetWlCallerInstructionAccounts,
            {
                params: {
                    newCaller,
                } satisfies types.SetWlCallerParams,
            } satisfies instructions.SetWlCallerInstructionArgs,
            this.program
        )
    }

    async isWhiteListed(
        connection: Connection,
        caller: PublicKey,
        commitmentOrConfig?: Commitment | GetAccountInfoConfig
    ): Promise<boolean> {
        try {
            const exceptedCaller = await this.getWhiteListCaller(connection, commitmentOrConfig)
            return exceptedCaller.equals(caller)
        } catch (e) {
            return false
        }
    }

    async getWhiteListCaller(
        connection: Connection,
        commitmentOrConfig?: Commitment | GetAccountInfoConfig
    ): Promise<PublicKey> {
        const [messageLibPDA] = this.deriver.messageLib()
        const messageLibInfo = await accounts.MessageLib.fromAccountAddress(
            connection,
            messageLibPDA,
            commitmentOrConfig
        )
        return messageLibInfo.wlCaller
    }

    validatePacket(
        _connection: Connection,
        endpointProgram: PublicKey,
        payer: PublicKey,
        encodedPacket: Uint8Array
    ): TransactionInstruction {
        const packet = PacketV1Codec.fromBytes(encodedPacket)
        const [receiveLibrary] = this.deriver.messageLib()
        const endpoint = new EndpointProgram.Endpoint(endpointProgram)

        return instructions.createValidatePacketInstruction(
            {
                payer: payer,
                receiveLibrary,
                anchorRemainingAccounts: endpoint.getVerifyIXAccountMetaForCPI(packet, receiveLibrary),
            } satisfies instructions.ValidatePacketInstructionAccounts,
            {
                params: { packet: encodedPacket } satisfies types.ValidatePacketParams,
            } satisfies instructions.ValidatePacketInstructionArgs,
            this.program
        )
    }

    /***
     * Get the account meta of the send instruction for CPI(Cross-Program Invocation )
     */
    async getSendIXAccountMetaForCPI(
        _connection: Connection,
        payer: PublicKey,
        _path: PacketPath
    ): Promise<AccountMeta[]> {
        const [msgLib] = this.deriver.messageLib()
        const ix = instructions.createSendInstruction(
            {
                endpoint: PublicKey.default, // useless
                messageLib: msgLib,
                payer,
            } satisfies instructions.SendInstructionAccounts,
            /// Fake value. params is useless.
            {
                params: {
                    packet: {
                        nonce: 0,
                        srcEid: 0,
                        sender: PublicKey.default,
                        dstEid: 0,
                        receiver: Array.from(PublicKey.default.toBytes()),
                        message: Uint8Array.from(Buffer.from('test')),
                        guid: Array.from(PublicKey.default.toBytes()),
                    } satisfies types.Packet,
                    options: Uint8Array.from(Buffer.from('test')),
                    nativeFee: 0,
                } satisfies types.SendParams,
            } satisfies instructions.SendInstructionArgs
        )
        ix.keys.forEach((key) => {
            if (!payer.equals(key.pubkey)) {
                key.isSigner = false
            }
        })

        return Promise.resolve(ix.keys.slice(1))
    }

    async getQuoteIXAccountMetaForCPI(
        _connection: Connection,
        _payer: PublicKey,
        _path: PacketPath,
        _commitment?: Commitment
    ): Promise<AccountMeta[]> {
        const [msgLib] = this.deriver.messageLib()
        const ix = instructions.createQuoteInstruction(
            {
                endpoint: PublicKey.default, // useless
                messageLib: msgLib,
            } satisfies instructions.QuoteInstructionAccounts,
            // Fake params, just to get ix.keys
            {
                params: {
                    packet: {
                        sender: msgLib,
                        dstEid: 1,
                        message: Uint8Array.from([]),
                        nonce: 0,
                        srcEid: 1,
                        receiver: Array.from(new Uint8Array(32)),
                        guid: Array.from(new Uint8Array(32)),
                    } satisfies types.Packet,
                    payInLzToken: false,
                    options: Uint8Array.from([]),
                } satisfies types.QuoteParams,
            } satisfies instructions.QuoteInstructionArgs
        )
        return Promise.resolve(ix.keys.slice(1))
    }

    /***
     * Get the account meta of the send instruction for CPI(Cross-Program Invocation )
     */
    async getInitConfigIXAccountMetaForCPI(payer: PublicKey, oappID: PublicKey, eid: number): Promise<AccountMeta[]> {
        const [sendConfig] = this.deriver.sendConfig(eid, oappID)
        const [receiveConfig] = this.deriver.receiveConfig(eid, oappID)
        const [messageLib] = this.deriver.messageLib()

        const ix = instructions.createInitConfigInstruction(
            {
                endpoint: PublicKey.default, // useless
                payer,
                messageLib,
                sendConfig,
                receiveConfig,
            } satisfies instructions.InitConfigInstructionAccounts,
            // Fake params, just to get the ix.keys
            {
                params: {
                    eid,
                    oapp: oappID,
                } satisfies types.InitConfigParams,
            } satisfies instructions.InitConfigInstructionArgs
        )
        ix.keys.forEach((key) => {
            if (!payer.equals(key.pubkey)) {
                key.isSigner = false
            }
        })
        return Promise.resolve(ix.keys.slice(1))
    }

    async getSetConfigIXAccountMetaForCPI(
        _endpointProgram: PublicKey,
        _oappID: PublicKey,
        _eid: number
    ): Promise<AccountMeta[]> {
        return Promise.reject(new Error('Method not implemented.'))
    }
}
